@import "../../assets/style/index.scss";

.hero {

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px 0 40px;

    @include onTablet {
      padding: 80px 0 228px;

      @include pageGrid
    }

  }

  &__desc {
    margin-bottom: 32px;
    text-align: center;
    line-height: 150%;

    @include onTablet {
      // align-self: start;
      // margin-right: 50%;
      margin-bottom: 16px;
      text-align: left;

      grid-column: span 3;
    }
  }
  
  &__title {
    font-size: 64px;
    line-height: 85%;
    letter-spacing: -3.2px;
    color: $color-font-mode-red;
    text-transform: uppercase;
    font-weight:400;
    text-align: center;

    @include onTablet {
      font-size: 80px;
      letter-spacing: -0.05em;

      position: absolute;
      left: 0;
      right: 0;
      bottom: 57%;

      // grid-column: span 8;
    }
  }

  &__img {

    @include onTablet {
      width: 326px;
      height: 328px;
      margin: 0 auto;
      grid-column: 2/-2;
      margin-bottom: 80px;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 18px 70px;
    border: 2px solid #F9512E;
    border-radius: 32px;
    color: #F9512E;

    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.32px;


    &::before {
      content: '';
      display: inline-flex;
      width: 20px;
      height: 20px;
      background-image: url("../../assets/img/hero/cookie-icon.svg");
    }

    @include onTablet {
      grid-column: 3/-3;
      justify-content: center;
    }
  }
}